@font-face {
    font-family: 'SFProDisplay';
    src:url('SFProDisplay-Bold.woff2') format('woff2');
    font-weight: bold;
    font-style: normal;
}


@font-face {
    font-family: 'SFProDisplay';
    src:url('SFProDisplay-Semibold.woff2') format('woff2');
    font-weight: 600;
    font-style: normal;
}


@font-face {
    font-family: 'SFProDisplay';
    src:url('SFProDisplay-Regular.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'CYGrotesk';
    src: url('cy-grotesk-grand-dark.otf');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'SFUIText-Light';
    src: url('SFUIText-Light.ttf') format('truetype');
    font-weight: lighter;
    font-style: normal;
}

@font-face {
    font-family: 'SFUIText-Italic';
    src: url('SFUIText-LightItalic.ttf') format('truetype');
    font-weight: lighter;
    font-style: italic;
}

@font-face {
    font-family: 'SFUIText-Regular';
    src: url('SFUIText-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

